.g-nav-icon,
.g-nav-icon--top {
  background-color: #f39c12;
  width: 50px;
  height: 39px;
  position: fixed;
  top: 25px;
  right: 10%;
  cursor: pointer;
  z-index: 500;
  backface-visibility: hidden;
}
.g-nav-icon--top {
  opacity: 0;
  transition: transform 600ms, opacity 900ms;
  transform: translate3d(0, 60px, 0);
}
@media screen and (max-width: 959px) {
  .g-nav-icon,
  .g-nav-icon--top {
    right: 7%;
  }
}
@media screen and (max-width: 479px) {
  .g-nav-icon,
  .g-nav-icon--top {
    top: 20px;
    right: 4%;
  }
}
.g-nav-icon__border--top {
  top: 10px;
}
.g-nav-icon__border--top,
.g-nav-icon__border--center-front,
.g-nav-icon__border--center-back,
.g-nav-icon__border--bottom {
  background-color: #fff;
  height: 3px;
  width: 30px;
  position: absolute;
  left: 10px;
}
.g-nav-icon__border--top,
.g-nav-icon__border--bottom {
  opacity: 1;
  transition: opacity 600ms linear;
}
.g-nav-icon__border--center-front,
.g-nav-icon__border--center-back {
  transition: transform 600ms;
  top: 18px;
}
.g-nav-icon__border--bottom {
  bottom: 10px;
}
.g-nav-icon.is-open .g-nav-icon__border--top,
.g-nav-icon--top.is-open .g-nav-icon__border--top,
.g-nav-icon.is-open .g-nav-icon__border--bottom,
.g-nav-icon--top.is-open .g-nav-icon__border--bottom {
  opacity: 0;
}
.g-nav-icon.is-open .g-nav-icon__border--center-front,
.g-nav-icon--top.is-open .g-nav-icon__border--center-front {
  transform: rotate3d(0, 0, 1, 45deg);
}
.g-nav-icon.is-open .g-nav-icon__border--center-back,
.g-nav-icon--top.is-open .g-nav-icon__border--center-back {
  transform: rotate3d(0, 0, 1, -45deg);
}
.g-nav-icon--top.is-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition-delay: 200ms;
}
.g-nav {
